.NavBar {
    z-index: 5;
    height: 6vh;
    width: 100vw;
    position: fixed;

    display: flex;
    justify-content: space-between;

    background-color: #1e1c1c;

    a {
        color: #fff;
        cursor: pointer;
        text-decoration: none;
        font-size: 1.1rem;
        font-weight: 400;

        span {
            width: 15%;
            height: 15%;

            position: relative;
            bottom: 1px;
        }
    }

    .nav-logo {
        flex: 1;
        display: flex;
        justify-content: baseline;
        align-items: center;

        padding-left: 3rem;

        a {
            display: flex;
            align-items: center;

            svg {
                width: 100px;
                height: 100px;

                path {
                    transition: all 0.3s ease-in-out;
                }
            }

            svg:nth-child(2) {
                width: 20%;
                height: 20%;
                padding-left: 0.5rem;
            }

            &:hover {
                path {
                    fill: #fff;
                }
            }
        }
    }

    .nav-links {
        flex: 0.5;
        display: flex;
        justify-content: space-around;
        align-items: center;
        justify-items: baseline;
        position: relative;
        right: 40%;
    }

    .nav-right {
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 15%;
        position: relative;
        right: 2%;

        svg {
            width: 20%;
            height: 20%;
        }
    }
}

.hover-pink {
    transition: all 0.3s ease-in-out;

    &:hover {
        color: pink;
    }
}

.hover-blue {
    transition: all 0.3s ease-in-out;

    &:hover {
        color: #2b4e87;
    }
}

.hover-orange {
    transition: all 0.3s ease-in-out;

    &:hover {
        color: #ee8067;
    }
}

.nav-contact {
    height: 40px;
    margin-top: 20px;

    a {
        span {
            display: inline-block;
            transition: transform 0.2s;
            top: 3px;
        }
    }
    &:hover {
        a {
            span {
                transform: rotate(180deg) translateX(-5px) translateY(-15px);
            }
        }
    }

    .nav-contact-drop-down {
        position: absolute;
        top: 50px;
        z-index: 5;
        width: 150px;
        height: 100px;
        border-radius: 15px;

        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: flex-start;

        background-color: #fff;
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;

        a {
            margin: 0 1rem;
            color: #000;
            transition: all 0.3s ease-in-out;

            &:hover {
                text-shadow: 1px 1px 1px;
            }
        }
    }
}
